import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndiciosComponent } from './indicios.component';

const routes: Routes = [
  {
    path: '',
    component: IndiciosComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class IndiciosRoutingModule { }
